ul {
    list-style: none;
    display: block;
    padding: 8px;
    margin: auto;
    padding: 15px;
    background-color: #61a7fd;
    font-weight: bold;
}

ul li {
    display: inline;
    vertical-align: middle;
    margin: 5px;
}

ul li a {
    padding: 8px;
    color: black;
    /* background-color: #117a8b; */
    border: 1px solid #117a8b;
    border-radius: 3px;
}

ul li .active {
    background-color: #117a8b;
    color: white;
    text-decoration: none;
}

ul li a:hover {
    background: #117a8b;
    color: white;
    text-decoration: none;
}

.home-icon {
    color: #ffc107;
}

.setu {
    color: red;
}

.message {
    background-color: lightblue;
    padding: 15px;
    font-size: 14px;
    font-weight: bold;
    text-align: center;
}