body {
    margin: 0px;
    padding: 0px;
    font-family: 'Raleway', sans-serif;
}

html, body {
    width: 100%
}

.last-updated {
    color: green;
    font-weight: bold;
    font-size: 12px;
}

.heading {
    font-weight: bold;
    font-size: 22px;
    margin-top: 10px;
}

.heading-data {
    text-align: center;
    padding: 5px;
}

.confirmed {
    color: red;
}

.active {
    color: lightcoral;
}

.recovered {
    color: green;
}

.deaths {
    color: #a0a3a7;
}

.count {
    transform: scale(1, 1.2);
    font-size: 30px;
    font-weight: bold;
}

.increment {
    margin-top: -10px;
    font-weight: bold;
}

.table-parent thead tr th {
    padding: 0.25rem !important;
    text-align: right;
    background-color: #cccccc;
    color: black;
}

.table-parent thead tr th:nth-child(1), .table-parent tbody tr td:nth-child(1) {
    text-align: center;
}

.table-parent thead tr th:nth-child(1), .table-parent tbody tr td:nth-child(1), .table-child thead tr th:nth-child(1), .table-child tbody tr td:nth-child(1) {
    text-align: left;
}

.table-child tbody tr td:nth-child(2) {
    text-align: right;
}

.table-parent tbody tr td {
    font-size: 14px;
    text-align: right;
    padding: 3px;
    color: black;
}

.table-parent tbody tr:hover {
    background-color: rgb(206, 203, 203);
}

.table-parent tbody tr:last-child {
    text-align: right;
}

.table-parent tbody tr:nth-child(even) {
    background-color: rgb(235, 235, 235);
}

.delta-confirmed {
    color: red;
    transform: scale(1, 0.8);
    font-size: 15px;
}

.delta-recovered {
    color: green;
    transform: scale(1, 0.8);
    font-size: 15px;
}

.delta-deaths {
    color: #545b62;
    transform: scale(1, 0.8);
    font-size: 15px;
}

.affected-states {
    font-weight: bold;
    color: blue;
}

.go-to-top {
    background-color: rosybrown;
    border: 1px solid rosybrown;
    border-radius: 2px;
    text-align: center;
    font-weight: bold;
    align-items: center;
    color: white;
}

.go-to-top:hover {
    background-color: #479bcf;
    border: 1px solid #479bcf;
    outline: none;
}

.table-child {
    border-collapse: collapse;
    margin-bottom: 10px;
}

table tr {
    cursor: pointer;
}

.hiddenRow {
    padding: 0 4px !important;
    background-color: white;
}

.dist-heading {
    color: green;
    margin: 5px;
    font-weight: bold;
    font-size: 12px;
    text-align: left;
}

.plus-minus {
    color: #6c757d;
}

.corona-free {
    font-weight: bold;
    max-width: 100%;
    color: #0062cc;
}
