.map-style {
    margin-top: 20px;
}

.state-data {
    color: red;
    padding: 5px;
    position: absolute;
    line-height: 10px;
    border-radius: 5px;
    box-shadow: 3px 3px 10px #0062cc;
    background-color: white;
    width: 47%;
    margin-top: -20px;
    left: 46%;
    text-align: center;
    font-weight: bold;
}

.state-data-heading {
    font-weight: bold;
    color: #0062cc;
}

.confirmed-data {
    color: red;
}

.recovered-data {
    color: green;
}

.deaths-data {
    color: #a0a3a7;
}

.active-data {
    color: lightcoral;
}

.cases {
    font-size: 10px;
}